.catalog-filter {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: var(--border-radius);
  height: 5rem;
  background: var(--c-light);
}

.catalog-filter__category-button.is-active {
  background: var(--c-accent);

}
.catalog-filter__title {
  font-size: 1rem;
}

.catalog-filter__title-img {
  width: 1.3rem;
  height: 1.3rem;
}

.catalog-filter__category-button {
  width: 8.1rem;
  font-size: 1rem;
}

.catalog-filter__input.input {
  width: 20%;
}