.input {
  --paddingX: 1rem;

  display: flex;
  align-items: center;
  width: 100%;
  height: var(--input-height);
  padding-left: var(--paddingX);
  padding-right: var(--paddingX);
  font-size: 1.5rem;
  color: var(--c-dark);
  background-color: var(--c-light);
  border: var(--border);
  border-radius: var(--border-radius);
}

.input::placeholder {
  color: var(--c-grey-3);
}

.input:hover {
  border-color: var(--c-dark);
}

.input:focus {
  border-color: var(--c-accent);
  outline: none;
}

.input.is-invalid {
  border-color: red;
}

textarea.input {
  --paddingY: calc(var(--input-height) / 8);
  height: 10rem;

  padding-top: var(--paddingY);
  padding-bottom: var(--paddingY);
  resize: none;
}

textarea {

  resize: none;
}

