.product {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;

}

.product__title {
  font-size: 2rem;
}

.product__description {
  font-size: 1rem;
}
.product__info {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  width: 100%;
}

.product__info-bottom {
  cursor: pointer;
}

product__image {
  font-size: 1rem;
  width: 30rem;
  height: 20rem;
}

.product__price-bottom {
  border-color: var(--c-grey);
  width: 2rem;
  height: 2rem;


}