.header {
  position: fixed;
  z-index: 100;
  top: 0;
  width: 100%;
  padding-top: 1rem;

  background-color: var(--c-light);

}



.header__menu-list {
  display: flex;
  justify-content: space-around;
  height: 100%;
  font-size: 2.5rem;
  list-style: none;
  gap: 1rem;
}
.header__menu-link {

  text-decoration: none;
  color: var(--c-dark);
}

.header__menu-logo {
  width: 14rem;
  height: 4rem;
}

.header__menu-link.is-active {
  color: var(--c-accent);
}