html,
body,
#root {
  min-height: 100vh;

}

body {
  font: var(--base-font-weight) var(--base-font-size)/var(--base-line-height) var(--base-font-family);
  color: var(--c-dark);
  background-color: var(--c-grey);
}

.content__inner.container {
  margin-right: auto;
  margin-left: auto;
  align-content: center;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;

}

.error {
  font-size: 0.7rem;
  color: red;
}

@media screen and (max-width: 1439px) {
  html {
    font-size: 0.95vw;
  }
}

@media screen and (max-width: 769px) {
  html {
    font-size: 1.302vw
  }
}
