.feedback-form {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  width: 40rem;
  margin-right: auto;
  margin-left: auto;
}

.feedback-form__title {
  font-size: 2.5rem;
}
.feedback-form__item-additional {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
}

.feedback-form__item-button {
  font-size: 1.8rem;
  text-align: center;
}