.product-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 16rem;
  height: 23rem;
  padding: 1rem;
  background: var(--c-light);
  border-radius: var(--border-radius);

}

.product-card__title {
  text-decoration: none;
  color: var(--c-dark);
  font-size: 1rem;

}

.product-card__title:hover {
  color: var(--c-accent);
}

.product-card__description {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  font-size: 1em;

}

.product-cart__image {
  width: 100%;
  height: 100%;
  object-fit: cover;

}
.product-card__info {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  width: 100%;
}

.product-card__price {
  font-size: 1.3rem;
}

.product-card__price-bottom {
  cursor: pointer;
}

.product-card__price-cart {
  width: 1.5rem;
  height: 1.5rem;
}

.product-card__price-cart:active {
  filter: invert(100%);
  -webkit-filter:invert(100%);
}